@charset "UTF-8";
/* 
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */
/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";
/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import '~@ionic/angular/css/display.css';
/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";
/** Entorno Web **/
/*** BASE  ***/
@import url("https://fonts.googleapis.com/css2?family=Indie+Flower&family=Nunito:wght@300;400;700;900&display=swap");
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

/** Media Queries **/
.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
ion-label,
ion-card-content,
ion-title,
button,
ion-buttons,
ion-card-subtitle,
ion-card,
ion-header,
h1,
h2,
h3 {
  font-family: "Nunito", sans-serif;
  font-weight: 300;
  text-align: left;
}

ion-label {
  font-size: 1.8rem;
}

ion-title {
  font-size: 1.9rem;
  font-weight: 700;
  text-transform: uppercase;
}

ion-card-content {
  font-size: 1.9rem;
  font-weight: 700;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ion-header ion-toolbar ion-title {
  padding: 0rem;
}

/*** UTILIDADES ***/
.contenedor {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .contenedor .login--inputs {
    display: flex;
    flex-direction: column;
    max-width: 75%;
    margin-left: 12%;
  }
}
.contenedor .login--contraseña {
  margin: auto;
  padding-top: 4rem;
  margin-right: -1rem;
  width: 75%;
}
@media (min-width: 1024px) {
  .contenedor .login--contraseña {
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
  }
}
.contenedor .login--contraseña ion-label {
  font-weight: 700;
  max-width: 100%;
}
.contenedor .login--contraseña ion-label a {
  color: #b63616;
}
.contenedor .boton-login {
  margin: auto;
  width: 100%;
  padding-top: 2rem;
  display: flex;
  justify-content: center;
}
.contenedor .boton-login ion-button {
  width: 100%;
}
@media (min-width: 1024px) {
  .contenedor .boton-login ion-button {
    width: 65%;
  }
}

.ocultar {
  display: none;
}

.reportes .reportes--datos {
  margin-left: 2rem;
}
.reportes .reportes--datos p {
  margin: 0;
  font-size: 1.4rem;
  font-weight: 700 !important;
  color: #000000 !important;
}
.reportes .reportes--datos ion-label {
  font-weight: 700;
  color: #000000;
}
.reportes .reportes--datos .lat_long {
  color: #6784dd !important;
}

@media (min-width: 1024px) {
  .ion-card-nosotros {
    font-size: 2.3rem;
  }
}

@media (min-width: 1024px) {
  .contenedor .nosotros ion-label {
    font-size: 2rem;
    color: 900;
  }
}
@media (min-width: 1024px) {
  .contenedor .nosotros--contenido ion-label {
    font-size: 1.9rem;
  }
}
.contenedor .nosotros--contenido .nosotros--contenido__label {
  font-weight: 700;
}

.recibos--bonos p {
  margin: 0;
}

.mensajeria {
  font-size: 1.8rem;
  margin-right: 2rem;
  margin-top: 3rem;
}
.mensajeria .mensajeria--elementos {
  width: 100%;
  color: black;
}
.mensajeria .mensajeria--elementos ion-label {
  padding-left: 1rem;
}
.mensajeria .mensajeria--elementos div {
  width: 100%;
}
.mensajeria .mensajeria--elementos div .parrafo {
  margin-top: 1rem;
  margin-left: 2rem;
  font-size: 1.4rem;
  text-align: start;
}